




































































import { Vue, Component } from 'vue-property-decorator'

@Component
export default class CodeDocs extends Vue {
  demo1: string = `
    function shuffle (arr) {
      let copy = arr.concat()
      let len = copy.length
      
      while (len) {
        let random = Math.floor(Math.random() * len--);
        [copy[random], copy[len]] = [copy[len], copy[random]]
      }

      return copy
    }
  `
  
  demo1Code: string = `
    <template>
      <o-code :text="demo1" lang="javascript"></o-code>
    </template>
    <code>
      data () {
        return {
          demo1: \`
            function shuffle (arr) {
              let copy = arr.concat()
              let len = copy.length
              
              while (len) {
                let random = Math.floor(Math.random() * len--);
                [copy[random], copy[len]] = [copy[len], copy[random]]
              }

              return copy
            }
          \`
        }
      }
    </code>
  `.replace(/code/g, 'script')

  demo2Code: string = `
    <template>
      <o-code lang="javascript">
        <pre>
          function shuffle (arr) {
            let copy = arr.concat()
            let len = copy.length
            
            while (len) {
              let random = Math.floor(Math.random() * len--);
              [copy[random], copy[len]] = [copy[len], copy[random]]
            }

            return copy
          }
        </pre>
      </o-code>
    </template>
  `

  Props: any = {
    head: [
      { prop: 'name', label: 'Name', width: 150 },
      { prop: 'description', label: 'Description', width: 200 },
      { prop: 'required', label: 'Required', width: 100 },
      { prop: 'type', label: 'Type', width: 350 },
      { prop: 'default', label: 'Default', width: 100 },
    ],
    body: [
      { name: 'text', description: '代码正文', required: 'false', type: 'String', default: `-` },
      { name: 'lang', description: '语言类型，为空则自动检测语言类型', required: 'false', type: 'String', default: `-` },
    ]
  }
}
